						<text style="font-size: 12px;font-weight: bold;color: #FFBF47;margin-left: 3px;">4.8</text>

						<image src="../../static/rank/dianping.png" mode="" style="width: 10px;height: 10px;margin-left: 8px">
						</image>
						<text style="font-size: 11px;color: #6f6f6f;margin-left: 3px;">点评 179</text>
						<image src="../../static/rank/renjun.png" mode="" style="width: 10px;height: 10px;margin-left: 8px">
						</image>
						<text style="font-size: 11px;color: #6f6f6f;margin-left: 3px;">人均 79</text>
					</view>

