				</view>
				<view style="color: #6f6f6f;font-size: 11px;margin-right: 10px;">
					11km
				</view>
				<view class="labellabel column" :style="index < 3 ? 'background-image: url(../../static/rank/top.png);' : 'background-image: url(../../static/rank/top_gray.png);'">
					<view style="font-size: 12px;">
						TOP
					</view>
					<view style="font-size: 16px;">
						{{index + 1}}
					</view>
				</view>
